.mainBlock {
    height: 100vh;
    background-color: #e3cace;
}
.container {
    border: 1px solid;
    height: 100%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.text {
    width: 400px;
    background-color: antiquewhite;
}
.photo {
    width: 300px;
    height: 400px;
    background-color: antiquewhite;
}